import MainPage from './components/MainPage';
import LoginForm from './components/LoginForm';
import Approved from './components/Approved';
import Restore from './components/Restore';
import Page404 from './components/Page404';
import Settings from './components/Settings';
import TestPage from './components/Learnings/TestPage/index';
import Calendar from './components/Calendar/index';

const routes = [
  {
    path: '/id:user_id',
    component: MainPage,
    description: ' '
  },
  {
    path: '/colleagues',
    component: MainPage,
    description: ' ()'
  },
  {
    path: '/news',
    component: MainPage,
    description: ''
  },
  {
    path: '/messages',
    component: MainPage,
    description: ' '
  },
  {
    path: '/tasks',
    component: MainPage,
    description: ''
  },
  {
    path: '/learnings',
    component: MainPage,
    description: ''
  },
  {
    path: '/settings',
    component: Settings,
    description: ' '
  },
  {
    path: '/calendar',
    component: Calendar,
    description: ''
  },
  {
    path: '/feed',
    component: MainPage,
    description: ''
  },
  {
    path: '/test/:any',
    component: TestPage,
    description: ' '
  },
  {
    path: '/login',
    component: LoginForm,
    description: '  / '
  },
  {
    path: '/approved',
    component: Approved,
    description: '  '
  },
  {
    path: '/Restore',
    component: Restore,
    description: '  '
  },
  {
    path: '/404',
    component: Page404,
    description: ' 404'
  },
  {
    path: '/:any', //    
    component: Page404,
    description: ' 404'
  }
]

export default routes;